.navbar {
  background-color: rgb(26, 26, 26);
  padding: 0.8rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.logo {
  background-color: rgb(245, 197, 24);
  font-family: sans-serif;
  font-weight: 900;
  letter-spacing: -1.5px;
  padding: 0.2rem;
  border-radius: 0.2rem;
}

.searchBar {
  box-sizing: border-box;
  border: none;
  padding: 0.4rem 0;
  border-radius: 0.2rem;
  width: 25rem;
  padding-inline: 0.3rem;
  /* outline: none; */
}

.searchBar:focus {
  outline-color: rgba(245, 197, 24);
  outline-offset: -2px;
}

.watchlist-logo {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0.2rem 1rem 0.2rem 1rem;
  border-radius: 0.2rem;
  cursor: pointer;
}

.watchlist-logo:hover {
  background-color: rgb(50, 50, 50);
}

.watchlist-icon {
  height: 28px;
  width: 28px;
  color: white;
}

.watchlist-text {
  color: white;
  padding-left: 0.3rem;
  line-height: 0;
}

.wathclist-counter {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
  background-color: red;
  color: white;
  padding: 0 0.2rem 0 0.2rem;
  border-radius: 0.4rem;
  margin-left: 0.3rem;
}
