.movie-card {
  display: flex;
  align-items: end;
  flex-shrink: 0 1;
  gap: 0.2rem;
}

.movie-details {
  padding-bottom: 1rem;
}

.staff {
  font-weight: bold;
}

.btn-add {
  margin-bottom: 1rem;
  border: none;
  border-radius: 0.2rem;
  padding: 0.4rem;
  background-color: blueviolet;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.star-rating {
  margin-top: 0.6rem;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
}

.starholder {
  display: flex;
}

.star {
  height: 1.6rem;
  width: 1.6rem;
  cursor: pointer;
  color: rgb(245, 197, 24);
  display: block;
  cursor: pointer;
}
