.watched {
  list-style: none;
  display: flex;
  gap: 10px;
  margin-top: 10px;
  padding-bottom: 2px;
  border-bottom: 2px solid grey;
}

.watched img {
  height: 100px;
  cursor: pointer;
  border: 1px solid grey;
}

.title {
  margin-bottom: 5px;
}
.watched-text,
.watched-rating {
  font-size: 15px;
  margin-bottom: 2px;
}

.remove {
  border: none;
  border-radius: 0.2rem;
  color: white;
  background-color: blueviolet;
  cursor: pointer;
  padding: 0.2rem;
}
